@-webkit-keyframes bounce {
  25% {
    -webkit-transform: translateY(-9px);
            transform: translateY(-9px); }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  75% {
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px); }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes bounce {
  25% {
    -webkit-transform: translateY(-9px);
            transform: translateY(-9px); }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  75% {
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px); }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

